import { Link } from "gatsby";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemText from "@mui/material/ListItemText";
import Divider from "@mui/material/Divider";
import List from "@mui/material/List";
import React, {Fragment} from "react"
import { sortBy } from "lodash";
import { Container, Typography } from "@mui/material";


const TopColleges: React.FC<any> = ({ data, location }) => {
  const {
    nodes
  } = data;
//   console.log(allRanks2021Json)
  // console.log(allStrapiCollege)
  const result = sortBy(nodes, ['OC', 'BC', 'BCM', 'MBCV', 'MBCDNC', 'MBC', 'SC', 'SCA', 'ST']);
  // const result = sortBy(allRanks2021Json.nodes, ['OC']);
  // console.log(result)
  // remove duplicates from array by coc props
  const collegesInRank = result.filter((item, index) => {
    return result.findIndex(({ coc }) => coc === item.coc) === index;
  }).map(item => {
    return item;
  });
  // console.log(collegesInRank)
  // from collegesInRank array split by first comma of con
  const collegesInRankSplit = collegesInRank.map(item => {
    return {
      ...item,
      con: item.con.split(',')[0]
    };
  })
  // console.log(collegesInRankSplit)


  return (
    <>    
    <Container sx={{ my: 2 }}>
    <Typography variant="body1" component="div">
    Top and best {location} colleges based on students choice, providing placement assistance, very good infrastructure, and excellent infrastructure. 
    </Typography>  
    </Container>
      <List>
        {collegesInRankSplit.map((node, index) => (
          <Fragment key={node.id}>
          <ListItem disablePadding>
            <ListItemButton component={Link} to={`/${node.coc}`}>
              <ListItemText
                primary={`${index +1 }. ${node.con} (${node.coc})`}
                // secondary={node.city}
              />
            </ListItemButton>
          </ListItem>
          <Divider />
        </Fragment>
        ))}
      </List>
      <Container sx={{ height: 100 }} />
    </>
  )
}

export default TopColleges
